.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.form_login{
  margin: auto;
  width: 40%;
}

.form_login h4{
  font-size: x-large;
  margin-top: 50px;
  margin-left: 120px;
}

.list_mobil{
  margin-top: 50px;
}
.tbl_mobil{
  border: 1px black solid;
  text-align: center;
  margin: auto;
}
.tbl_mobil td{
  left: auto;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
